  z-index: 1;
}
.widget__tail {
  position: absolute;
  bottom: 5px;
  right: 5px;
  border-radius: 5px;
  padding: 5px;
  z-index: 1;
}
.widget__tail:after {
  content: '';
  position: absolute;
  border: 5px solid transparent;
  border-top: 5px solid #fff;
  border-right: 5px solid #fff;
}
.widget__hint {
  padding: 10px 20px;
  position: absolute;
  right: 50px;
  bottom: 30px;
  width: 240px;
  border-radius: 30px;
  background-color: #222222;
  color: #fff;
  z-index: 0;
}
.widget__hint.dark {
  color: #000;
}
.widget__hint input {
  background-color: transparent;
