  background-color: #215fdc;
  visibility: hidden;
}
.widget__eyes:before {
  content: '';
  display: block;
  height: 12px;
  width: 12px;
  border-radius: 8px;
  visibility: visible;
  background-color: inherit;
}
.widget__eyes:after {
  content: '';
  display: block;
  height: 12px;
  width: 12px;
  border-radius: 8px;
  visibility: visible;
  background-color: inherit;
}
.widget__container.animated .widget__eyes:before,
.widget__container.animated .widget__eyes:after {
  position: relative;
  -webkit-animation: blink 6s infinite;
          animation: blink 6s infinite;
}
@-webkit-keyframes blink {
  18% {
    height: 12px;
  }
  20% {
    height: 0;
  }
  22% {
    width: 12px;
    height: 12px;
  }
  24% {
    height: 0;
  }
  26% {
    width: 12px;
    height: 12px;
  }
  65%{
    right: 0;
  }
  66% {
    right: 6px;
  }
  76% {
    right: 6px;
  }
  78% {
    right: -6px;
  }
  88% {
    right: -6px;
  }
  90% {
    right: 0;
  }
}
@keyframes blink {
